.header {
  display: flex;
  justify-content: center;
}

.logo {
  width: 45px;
  height: 35px;
}

.form {
  border: #eee 1px solid;
  border-radius: 0.5rem;
  background-color: #152238;
  padding: 25px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.error {
  color: red;
}

.success {
  color: lime;
}

h2,
p {
  color: white;
}

.displayInputBtn {
  border-radius: 50%;
  padding: 2px;
  margin: 2px;
  width: 30px;
  height: 30px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
}

input {
  margin: 10px 0;
  width: 100%;
  padding: 5px;
}

.addTaskBtn {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  color: white;
  padding: 5px;
  margin: 5px;
  cursor: pointer;
  font-size: 0.8rem;
}
