.task {
  display: flex;
  justify-content: space-between;
}

.buttonsImg {
  text-align: center;
  padding: 0;
}

.deleteBtn,
.doneBtn {
  width: 30px;
  vertical-align: middle;
  cursor: pointer;
}

.deleteBtn {
  margin: 0 5px;
}

.deleteBtn:hover,
.doneBtn:hover {
  border: 1px solid #eee;
}
